import "./App.css";
import { ChakraProvider, extendTheme } from "@chakra-ui/react";
import Portfolio from "./Portfolio";

function App() {
  const theme = extendTheme({
    config: {
      initialColorMode: "dark",
      useSystemColorMode: true,
    },
  });

  return (
    <ChakraProvider theme={theme}>
      <div className="App">
        <Portfolio />
      </div>
    </ChakraProvider>
  );
}

export default App;
